import "../App.css";
import * as React from "react";
import { useNavigate } from "react-router-dom";
import { BsYoutube } from "react-icons/bs";
import { BsInstagram } from "react-icons/bs";
import { BsFacebook } from "react-icons/bs";
import MenuIcon from "@mui/icons-material/Menu";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";

function Header() {
  let navigate = useNavigate();

  return (
    <>
      <div className="head-container">
        <h3
          className="header-logo"
          onClick={() => {
            navigate("/");
          }}
        >
          용두산<br></br>미디어파크
        </h3>
        <div className="header-menu">
          <div
            className="header-items"
            onClick={() => {
              navigate("/contents");
            }}
          >
            콘텐츠
          </div>
          <div className="header-items">가상체험관</div>
          <div className="header-items">공지사항</div>
          <div
            className="header-items"
            onClick={() => {
              navigate("/yongdusan");
            }}
          >
            용두산공원
          </div>
        </div>
        <div className="header-burger">
          <BasicMenu />
        </div>
        <div className="header-sns">
          <div className="header-items">
            <BsInstagram className="icon" size="24" />
          </div>
          <div className="header-items">
            <BsFacebook className="icon" size="24" />
          </div>
          <div className="header-items">
            <BsYoutube className="icon" size="30" />
          </div>
        </div>
      </div>
    </>
  );
}

function BasicMenu(props) {
  let navigate = useNavigate();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div>
      <MenuIcon
        id="basic-button"
        aria-controls={open ? "basic-menu" : undefined}
        aria-haspopup="true"
        aria-expanded={open ? "true" : undefined}
        onClick={handleClick}
        fontSize="large"
      ></MenuIcon>
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          "aria-labelledby": "basic-button",
        }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "right",
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "right",
        }}
      >
        <MenuItem
          onClick={() => {
            navigate("/contents");
          }}
        >
          콘텐츠
        </MenuItem>
        <MenuItem onClick={handleClose}>가상체험관</MenuItem>
        <MenuItem onClick={handleClose}>공지사항</MenuItem>
        <MenuItem
          onClick={() => {
            navigate("/yongdusan");
          }}
        >
          용두산공원
        </MenuItem>
      </Menu>
    </div>
  );
}

export default Header;
