import "../App.css";
import React, { useState, useEffect } from "react";
import axios from "axios";
import yongdusan from "../img/yongdusan.png";

function App() {
  let [temp, setTemp] = useState("");
  let [hum, setHum] = useState("");
  let [windSpeed, setWindSpeed] = useState("");
  let [windDeg, setWindDeg] = useState("");

  useEffect(() => {
    Promise.all([
      axios
        .get(
          "https://yongdo.azurewebsites.net/api/get_sensor_data?code=6iLwEWIclgnzQnusL3es-sP-t_IQ7iF7nlRnjbrbL5jlAzFu2s-QPQ==&weather_station=YeongdoSchool"
        )
        .then((res) => {
          let copy1 = [res.data.temperature];
          // console.log(res.data);
          setTemp(copy1);
        })
        .catch(() => {
          console.log("받지못함");
        }),
      axios
        .get(
          "https://yongdo.azurewebsites.net/api/get_sensor_data?code=6iLwEWIclgnzQnusL3es-sP-t_IQ7iF7nlRnjbrbL5jlAzFu2s-QPQ==&weather_station=YeongdoSchool"
        )
        .then((res) => {
          let copy2 = [res.data.humidity];
          setHum(copy2);
        })
        .catch(() => {
          console.log("받지못함");
        }),
      axios
        .get(
          "https://yongdo.azurewebsites.net/api/get_sensor_data?code=6iLwEWIclgnzQnusL3es-sP-t_IQ7iF7nlRnjbrbL5jlAzFu2s-QPQ==&weather_station=YeongdoSchoolExperiment"
        )
        .then((res) => {
          let copy3 = [res.data.wind_speed];
          setWindSpeed(copy3);
        })
        .catch(() => {
          console.log("받지못함");
        }),
      axios
        .get(
          "https://yongdo.azurewebsites.net/api/get_sensor_data?code=6iLwEWIclgnzQnusL3es-sP-t_IQ7iF7nlRnjbrbL5jlAzFu2s-QPQ==&weather_station=YeongdoSchoolExperiment"
        )
        .then((res) => {
          let copy4 = [res.data.wind_deg_status];
          setWindDeg(copy4);
        })
        .catch(() => {
          console.log("받지못함");
        }),
    ]);
  }, [windSpeed]);

  return (
    <>
      <Temp temp={temp} hum={hum} windSpeed={windSpeed} windDeg={windDeg} />
    </>
  );
}

function Temp(props) {
  return (
    <>
      <div className="contents-container weather">
        <img src={yongdusan} alt="title" className="img" />

        <div className="styleCard">
          <div className="card">
            <div className="styleCardTitle">온도</div>
            <div className="styleCardTitle">{props.temp}</div>
          </div>
          <div className="card">
            <div className="styleCardTitle">습도</div>
            <div className="styleCardTitle">{props.hum}</div>
          </div>
          <div className="card">
            <div className="styleCardTitle">풍속</div>
            <div className="styleCardTitle">{props.windSpeed}</div>
          </div>
          <div className="card">
            <div className="styleCardTitle">풍향</div>
            <div className="styleCardTitle">{props.windDeg}</div>
          </div>
        </div>
      </div>
    </>
  );
}

export default App;
